



























import * as echarts from '../../components/uni/echarts/echarts.simple.min.js';
import mpvueEcharts from '../../components/uni/mpvue-echarts/src/echarts.vue';

export default {
	data() {
		return {
			scrolling:0,
			count: 20,
			list: [],
			propertyId:'',
			machineId:'',
			machineName: '',
			propertyName: '',
		}
	},
	onLoad: function(e){
		let that = this;
		console.log(e);
		this.propertyId = e.propertyId;
		this.propertyName = e.propertyName;
		this.machineId = e.machineId;
		this.machineName = e.machineName;
		uni.setNavigationBarTitle({
			title: e.propertyName
		});
		this.getProperty_now();
	},
	onReady: function(){},
	onShow: function(){},
	onHide: function(){},
	onUnload: function(){},
	updated: function(){},
	beforeUpdate: function(){},
	//下拉动作
	onPullDownRefresh: function(){
		let that = this;
		setTimeout(function(){
			uni.stopPullDownRefresh();
			that.count = 20;
			that.getProperty_now();
			uni.showToast({
				title: '刷新成功',
				duration: 2000,
				icon: "success",
			});
		},2000)	
	},
	//上拉触底
	onReachBottom: function(){
		this.count = this.count + 20;
		this.getProperty_now();
	},
	//滚动
	onPageScroll: function(e){
		console.log(e)
	},
	methods: {
		getProperty_now: function(){
			let that = this;
			wx.request({
				url: 'https://38798879.xyz/api/Machine/GetLatestRTTags', 
				data: {
					"Inputs": [
						{
							"GetTagsType": 0,
							"MachineId": that.machineId,
							"TagId": that.propertyId,
							"QueryType": 0,
							"StatisticType": 0,
							"StartTime": '',
							"EndTime": '',
							"Count": that.count,
						}
					]
				},
				method: 'POST',
				success: (res) => {
					console.log(res);
					that.list = res.data[0].Tags;
				},
				fail: (res) => {
					console.log(res);
				}
			});
		},
	},
	components: {
		mpvueEcharts
	},
}
